.container-main {
  max-width: 900px;
  margin: 0 auto;
  padding: 30px 0;
  font-family: Montserrat;
}

.remove-style-link a:link {
  color: black;
  text-decoration: none;
}

.remove-style-link a:visited {
  color: black;
  text-decoration: none;
}

.remove-style-link a:hover {
  color: black;
  text-decoration: none;
}

.remove-style-link a:active {
  color: black;
  text-decoration: none;
}

.about {
  padding-top: 40px;
  line-height: 30px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.about img {
  width: 350px;
  padding-bottom: 40px;
}

.about .text-about {
  padding: 20px 0 0 30px;
  max-width: 500px;
}

.services-container {
  padding: 15px;
  border-radius: 10px;
}

.services-card {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding: 15px;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

.card-service {
  width: 150px;
  text-align: center;
  margin-bottom: 15px;
}

.img-service {
  width: 150px;
  height: 150px;
  -o-object-fit: cover;
     object-fit: cover;
  padding-bottom: 20px;
}

.social-links {
  background-color: #8c52ff;
  background-image: -webkit-gradient(linear, left top, right bottom, from(#8c52ff), to(lightgray));
  background-image: linear-gradient(to bottom right, #8c52ff, lightgray);
  height: 200px;
  padding: 30px 15px;
  color: white;
  font-family: Montserrat;
  text-align: center;
}

.text-schedule {
  width: 350px;
  padding: 25px 25px 0;
}

.form-schedule {
  background-color: #f8f8f8;
}

@media (max-width: 970px) {
  .container-main {
    width: 93%;
    margin: 0 auto;
    padding: 30px 0;
  }
  .card-service {
    width: 250px;
  }
  .services-card {
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
  }
  .text-about {
    padding: 20px 0 0 0;
    max-width: 500px;
  }
}

@media (max-width: 400px) {
  .about img {
    width: 90%;
  }
}
